/*    Testimonials  */
.testimonials {
  background: url(../assets/testimonial_background.png) repeat center center;
  background-color: #FACA36;
  background-size: contain;
  min-height: 60vh; }
  .testimonials h2 {
    padding: 70px 0 0 0;
    color: #e2e1e1;
    text-align: center;
    margin-top: 0; }
  .testimonials blockquote {
    padding: 0 3rem 1rem 3rem;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
    quotes: "“" "”" "‘" "’";
    z-index: 1; }

blockquote:before {
  color: #FFFFFF;
  content: open-quote;
  line-height: 0.1em;
  margin-right: 0.15em;
  vertical-align: -0.4em;
  font-family: Georgia, serif;
  font-size: 90px;
  font-weight: bold;
  z-index: 0; }

.subject {
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px; }
  .subject .name {
    color: black;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 20px;
    padding: 20px 20px; }
  .subject span {
    color: black;
    font-size: 20px; }

/*     Contact*/
.contact-main {
  max-width: 1000px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-gap: 10px;
  margin: 0 auto; }

.contact {
  background-color: #f4f4f4;
  min-height: 100vh;
  padding: 0; }
  .contact h2 {
    padding: 70px 0 0 0;
    text-align: center;
    margin-top: 0; }
  .contact span {
    color: #101311; }

.contact-form {
  margin: 0 0 0 2rem; }

.contact-info li {
  margin: 25px 0;
  list-style: none; }

.p-contact {
  color: #373737;
  font-family: "Quicksand", Helvetica, Arial, sans-serif;
  font-size: 20px;
  line-height: 24px; }

.fa {
  color: #daa501;
  width: 38px;
  height: 37px;
  border: 2px solid #daa501;
  border-radius: 50%;
  padding: 4px;
  text-align: center;
  margin-right: 1rem; }

/*   Footer */
footer {
  position: absolute;
  height: 100px;
  width: 100%;
  background-color: #facb36; }
  footer li {
    display: inline-block; }
  footer .fa {
    color: #f4f4f4;
    width: 38px;
    height: 37px;
    border: 2px solid #f4f4f4;
    border-radius: 50%;
    padding-top: 8px;
    text-align: center;
    margin-right: 20px;
    text-decoration: none; }

.social-icons {
  float: right;
  margin-top: 20px; }

.credits {
  float: left;
  margin-top: 20px;
  margin-left: 30px;
  color: #f4f4f4; }

@media screen and (max-width: 640px) {
  /*    Footer*/
  footer {
    position: absolute;
    height: 140px;
    width: 100%;
    background-color: #facb36; }
  .social-icons {
    float: left;
    padding-top: 20px;
    padding-left: 30px; } }
